@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed {
		@include scroll-blocked(); } }

body {
	position: relative;
	font-family: $font-norm;
	@include font-light;
	@include font($font-size, $line-height);
	color: $text-color;
	min-width: $page-layout-min;
	//background: $bg-color url(../../img/empty.png) no-repeat left top fixed
	background: $bg-color url(../../img/bg-page.png) center center;

	// выделение текста
	//*::selection
	//ackground-color: white
 }	//olor: $base-color

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	@include font-light;
	padding: 0;
	color: $title-color;
	margin: 0 0 $bottom-space--element;

	@include media('<mob-lg') {
		margin-bottom: round-float($bottom-space*0.85, 1); }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;

		&:hover {
			text-decoration: underline; } } }

h1, .h1 {
	@include font-h1; }

h2, .h2 {
	@include font-h2; }

h3, .h3 {
	@include font-h3; }

h4, .h4 {
	@include font-bold;
	@include font($font-size-h4, $line-height-h4); }

h5, .h5 {
	@include font($font-size-h5, $line-height-h5); }

h6, .h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: none;
	cursor: default;

	&:link {
		cursor: pointer; }

	&:not([class*="btn-"]):hover {
		text-decoration: none;
		color: $link-color--hover; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-medium; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.highlight {
	color: $highlight-color; }

.text-color-alt {
	color: $base-color-alt; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

.hide {
	display: block; }

.section, .section-bottom {
	margin-bottom: $bottom-space--section; }

.article, .article-bottom {
	margin-bottom: $bottom-space--article; }

.element-bottom {
	margin-bottom: $bottom-space--element; }

.row-bottom {
	margin-bottom: $bottom-space--row; }

