/* list */
.#{$user-content} {
	//list
	ul, ol {
		margin: $bottom-space--element 0;
		padding: 0;
		list-style: none;

		li {
			padding-left: $list-space;
			margin: 0 0 $bottom-space;
			position: relative; } }

	ul > li::before {
		content: '';
		display: block;
		float: left;
		@include size(12px);
		margin: 0.35em 0 -0.35em -#{$list-space};
		border-radius: 6px;
		background-color: $highlight-color; }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;
			height: 1%;
			overflow: hidden;

			&::before {
				position: relative;
				display: inline-block;
				text-align: left;
				@include font-medium;
				color: $highlight-color;
				content: counter(section);
				padding: 0 4px 0 0;
				min-width: $list-space;
				margin-left: -#{$list-space}; } } }

	ul, ol {
		ul, ol {
			margin: round($bottom-space*0.5) 0; } } }

/* list reset */
.reset-list,
.#{$user-content} .reset-list {
	list-style: none;

	li {
		&, &:first-child {
			padding: 0;

			&::before {
				content: none; } } } }

/* list reset end */
/* list end */

/* table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;

	td, th {
		@include font($font-size, $line-height);
		color: inherit;
		padding: 8px 15px;
		border-color: $text-color; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

.#{$user-content} {
	table {
		margin-bottom: $bottom-space; }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td, th {
			color: $text-color;
			background-color: $bg-color;
			border: 0 solid $base-color;
			@include font(1.4rem, 1.5);

			&:first-child, &:first-child {
				border-left-width: 1px; }

			&:last-child, &:last-child {
				border-right-width: 1px; } }

		tr:last-child {
			td {
				border-bottom-width: 0; } }

		td {
			border-top-width: 1px; }

		tbody tr:first-child td {
			border-top-width: 0; }

		tbody tr:nth-child(even) td {
			background: $base-color;
			color: $bg-color; }

		th, thead td {
			@include font-bold;
			font-size: 1.6rem;
			text-align: left; }

		th, thead td, tfoot td {
			background: $base-color;
			color: $bg-color;
			border-color: $base-color;
			border-top-width: 0; } } }

/* table end */

/* blockquote */
blockquote {
	$q-size: 5px 46px; //h w
	$q-rotate: -40deg;

	display: block;
	position: relative;
	width: 469px;
	margin: $bottom-space--element auto;
	padding: 0;
	//background-color: lightcoral

	q {
		display: block;
		position: relative;
		margin: 0;
		padding: 9rem 0;
		font-family: inherit;
		@include font-medium;
		@include font(3.0rem, round-float(36/30, 2));
		color: $text-color; }

	cite {
		position: absolute;
		bottom: 5rem;
		left: 0;
		text-align: left;
		display: block;
		margin-left: auto;
		font-family: inherit;
		@include font-italic;
		@include font($font-size, $line-height);
		color: $highlight-color; }

	&, & q {
		&::before, &::after {
			content: "";
			display: block;
			position: absolute;
			left: 50%;
			height: nth($q-size, 1);
			width: nth($q-size, 2);
			margin-left: -#{round(nth($q-size, 2)/2)};
			background-color: $highlight-color;
			transform: rotate($q-rotate); }

		&::before {
			top: 0;
			margin-top: 14px; }

		&::after {
			bottom: 0;
			margin-bottom: 14px; } }

	q {
		&::before {
			margin-top: 30px; }

		&::after {
			margin-bottom: 30px; } } }

/* blockquote end */

