/* columns */
.cols {
	position: relative;
	@include clear;
	margin: $bottom-space--article 0 $bottom-space--section;

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		top: 8px;
		bottom: 0;
		border-right: 1px solid $text-color;
		opacity: 0.2; } }

.col {
	float: left;
	width: calc(50% - #{nth($grid-space, 2)});
	margin: 0 nth($grid-space, 2);

	&:nth-child(odd) {
		margin-left: 0; }

	&:nth-child(even) {
		margin-right: 0; } }

/* columns end */

.row {
	@include clear;

	.col {
		float: left;
		width: 100%; }


	.col-6 {
		width: 50%; } }
