/* === BUTTON === */
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
}

[class|="btn"] input[type="submit"], [class|="btn"] input[type="button"], [class|="btn"] input[type="reset"], [class*=" btn-"] input[type="submit"], [class*=" btn-"] input[type="button"], [class*=" btn-"] input[type="reset"] {
	background: url("../img/empty.png") repeat;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	padding: 0;
	border: none;
	z-index: 99;
	opacity: 0;
}

/* btn-default */
[class*="btn-def"] {
	position: relative;
	padding: 18px 46px 18px 32px;
	margin: 0;
	border: none;
	border-radius: 30px;
	background: #6bdbbc;
	box-shadow: none;
	font-family: "Roboto", sans-serif;
	color: white;
	text-align: left;
	transition-property: color, background-color;
	transition-duration: 0.2s;
}

[class*="btn-def"]::before, [class*="btn-def"]::after {
	content: "";
	display: block;
	position: absolute;
	right: 32px;
	width: 7px;
	height: 1px;
	margin: 2px -1px;
	background-color: #333399;
}

[class*="btn-def"]::before {
	bottom: 50%;
	transform: rotate(45deg);
}

[class*="btn-def"]::after {
	top: 50%;
	transform: rotate(-45deg);
}

[class*="btn-def"], [class*="btn-def"] > span {
	font-size: 18px;
	line-height: 24px;
}

[class*="btn-def"]:hover:not(:disabled):not(.disabled) {
	background-color: #5eb8b2;
}

[class*="btn-def"]:disabled, [class*="btn-def"].disabled {
	cursor: default;
	color: #969696;
	background-color: #ccc;
}

[class*="btn-def"]:disabled::before, [class*="btn-def"]:disabled::after, [class*="btn-def"].disabled::before, [class*="btn-def"].disabled::after {
	background-color: currentColor;
}

[class*="btn-def"]:disabled path, [class*="btn-def"].disabled path {
	fill: currentColor;
}

/* btn-default end */
/* btn with icon */
.btn-def_icon_right::before, .btn-def_icon_right::after, .btn-def_icon_left::before, .btn-def_icon_left::after {
	display: none;
}

.btn-def_icon_right svg, .btn-def_icon_left svg {
	position: absolute;
	display: block;
	top: 50%;
}

.btn-def_icon_right svg path, .btn-def_icon_left svg path {
	fill: #333399;
}

.btn-def_icon_right {
	padding-right: 60px;
}

.btn-def_icon_right svg {
	right: 38px;
	transform: translate(50%, -50%);
}

.btn-def_icon_left {
	padding-left: 60px;
}

.btn-def_icon_left svg {
	left: 38px;
	transform: translate(-50%, -50%);
}

/* btn with icon end */
/* btn-default-alt */
.btn-default-alt {
