.heading {
	text-align: center;
	padding: 7rem 0 0;
	margin-bottom: 4rem;
}

.c-heading {
	opacity: 0;
	transition-property: opacity, animation;
	transition-duration: 0.8s;
	transition-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
	animation-duration: 1s;
	animation-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
}

.c-heading.is-active {
	opacity: 1;
	transition-delay: 0.2s;
	animation-fill-mode: forwards;
	animation-name: showHeading;
}

.c-heading.is-leaving {
	opacity: 0;
	animation-fill-mode: forwards;
	animation-duration: 0s;
	animation-delay: 1s;
	animation-name: hideHeading;
	transition-timing-function: ease-in-out;
}

@keyframes showHeading {
	0% {
		transform: translate(0, 60vh);
	}
	30% {
		transform: translate(0, 60vh);
	}
	100% {
		transform: translate(0, 0);
	}
}

@keyframes hideHeading {
	0% {
		transform: translate(0, 0);
	}
	100% {
		transform: translate(0, 60vh);
	}
}

.c-transition {
	background-color: #2a2971;
	cursor: wait;
	height: 100vh;
	left: 0;
	overflow: hidden;
	pointer-events: none;
	position: fixed;
	top: 100vh;
	transform: translate3d(0, 100%, 0);
	width: 100%;
	z-index: 1100;
}

.c-transition .u-h1 {
	left: 50%;
	position: absolute;
	text-align: center;
	top: 50%;
	transform: translate(-50%, 100vh);
	transition: transform 1s cubic-bezier(0.4, 0.6, 0.2, 1);
	transition-delay: 0.2s;
}

.c-transition.is-active {
	animation-delay: 0s;
	animation-duration: 1s;
	animation-fill-mode: forwards;
	animation-name: fade-in;
	animation-timing-function: cubic-bezier(0.55, 0.085, 0, 0.99);
	top: 0;
}

.c-transition.is-active .u-h1 {
	transform: translate(-50%, -50%);
}

.c-transition.is-leaving {
	animation-duration: 1s;
	animation-name: fade-out;
}

.c-transition.is-leaving .u-h1 {
	transform: translate(-50%, -100vh);
	transition-delay: 0s;
}

@keyframes fade-out {
	0% {
		transform: translateZ(0);
	}
	to {
		transform: translate3d(0, -100%, 0);
	}
}

@keyframes fade-in {
	0% {
		transform: translate3d(0, 100%, 0);
	}
	to {
		transform: translateZ(0);
	}
}

