/* slick sliders */
// slider
.slick-slide {
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
	-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
	outline: none !important; }

// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $base-color;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition-property: transform, opacity;
				transition-duration: $animation-speed; } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* images slider */
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--section; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background-color: transparent;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1;
		margin-bottom: $bottom-space; }

	&::before {
		content: '';
		display: block;
		@include ratio(16, 9);

		.no-js & {
			content: none; } }

	img {
		position: absolute;
		@include position(0);
		text-align: center;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(contain);

		.no-js &[data-lazy] {
			display: none; }

		.no-js & {
			position: relative;
			@include position(auto);
			width: auto;
			height: auto;
			@include object-fit(fill);
			margin-bottom: 1rem; } }

	.caption {
		display: none;

		.no-js & {
			display: block; } } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition-property: opacity, transform;
				transition-duration: $animation-speed;

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */

/* flashes */
.flashes {
	padding: 0 90px; }

.flashes__item, .images-slider__item .caption {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/* flashes end */

/* breadcrumbs */
.breadcrumbs {
	margin-bottom: $bottom-space--section;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--element; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	& {
		cursor: default;
		text-align: center;
		@include font($font-size--small, $line-height--small); }

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '/';
			border: none;
			color: $highlight-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $highlight-color;
		text-decoration: none;
		cursor: pointer;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		//span
		//order-bottom: 1px solid transparent
		/// transition
		//prop: border-color
		//ransition:
		//property: $prop
		//duration: $animation-speed

		&:hover {
			color: $highlight-color--hover;
			//text-decoration: underline

			//span
 } } }			//order-color: $highlight-color

/* breadcrumbs end */

/* social networks */
.social-networks {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -10px -20px 0; }

.social-networks__item {
	float: left;
	margin: 0 10px 20px;

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px); }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks end */

/* social networks in footer */
.f-soc {
	overflow: hidden;
	margin-bottom: $bottom-space--element;

	&:last-child {
		margin-bottom: 0; } }

.f-soc__group {
	float: left;
	margin: 0 40px 40px 0;

	& > strong {
		display: block;
		margin-bottom: 5px;
		font-size: 14px;
		line-height: 18px;
		color: $text-color-light; } }

.f-soc__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -15px -15px 0; }

.f-soc__item {
	float: left;
	margin: 0 15px 15px 0;

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px); }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks in footer end */

/* developer */
.developer {
	line-height: 0;

	a {
		display: inline-block;
		vertical-align: top;
		color: $highlight-color;
		text-decoration: none;
		// transition
		transition: color $animation-speed ease-in-out;

		&:hover {
			color: $highlight-color--hover;
			text-decoration: none; }

		svg {
			display: inline-block;
			vertical-align: top;
			@include size(15px, 70px);
			margin-right: 5px;

			path {
				fill: currentColor; } }

		& > span {
			display: inline-block;
			vertical-align: top;
			font-size: 14px;
			line-height: 15px; } } }

/* developer end */

/* subscription form */
.subs-form {
	margin-bottom: $bottom-space--element;

	&:last-child {
		margin-bottom: 0; }

	form {
		display: block; }

	.label-holder, label, .error-note, .success-note {
		font-size: 14px;
		margin-bottom: 2px; }

	.input-holder {
		&.error {
			&::before, &::after {
				opacity: 0; } }

		&.success {
			&::before, &::after {
				background-color: $base-color;
				right: 17px; } } }

	button {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		@include size($form-input-height);
		margin: 0;
		border: none;
		box-shadow: none;
		background-color: $highlight-color;
		border-radius: round($form-input-height / 2);
		color: $base-color;
		// transition
		$prop: color, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; }

		&:hover {
			background-color: $highlight-color--hover; }

		svg {
			$size: 24px;
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			@include size($size);
			margin: -#{round($size / 2)} 0 0 -#{round($size / 2)};
			// transition
			$prop: opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed;
				timing-function: ease-in-out; }

			path {
				fill: currentColor; } } }

	// Form error
	.error ~ button {
		background-color: $form-error-color; }

	// Form success
	.success ~ button {
			svg {
				opacity: 0; } }

	// Form complete
	.form-success {
		.success-note {
			display: block; }

		input[type="email"], input[type="input"] {
			&:not(:disabled) {
				background-color: #56a4a6;
				border-color: #56a4a6; } } } }

/* subscription form end */

/* footer list */
.f-list {
	list-style: none;
	padding: 0;
	margin: 0 0 $bottom-space--element;
	font-size: 2.0rem;
	line-height: round-float(24/20, 2);

	&:last-child {
		margin-bottom: 0; }

	li {
		margin-bottom: 1.5rem;

		&:last-child {
			margin-bottom: 0; }

		& > div {
			margin-bottom: 1rem;

			&:last-child {
				margin-bottom: 0; } } }

	a {
		color: inherit;
		// transition
		transition: color $animation-speed ease-in-out;

		&:hover {
			color: $link-color; }

		&[href^="tel:"] {
			b {
				@include font-bold;
				font-size: 24px; } } } }

/* footer list end */

/* requisites */
.req {
	font-size: 12px;
	line-height: 14px;
	color: $text-color-light;

	p {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0; }

		b {
			color: white; } } }

/* requisites end */

/* footer form */
.f-form {
	margin: 0 0 $bottom-space--element;
	padding: 40px;
	background-color: $base-color-alt;

	&:last-child {
		margin-bottom: 0; }

	button {
		display: block;
		width: 100%; } }

.f-form__cols {
	display: flex;
	justify-content: space-between;
	align-items: flex-start; }

.f-form__col {
	width: calc(50% - 40px / 2); }

/* footer form end */
