@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === LAYOUT === */
html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<tab-lg') {
			@include scroll-blocked; } } }

.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.layout-space {
	@include padding-horizontal(nth($page-layout-space, 2));

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
//.layout-article
.#{$user-content} {
	& > p, & > ul, & > ol,
	& > h2, & > h3, & > h4, & > h5, & > h6,
	& > .h2, & > .h3, & > .h4, & > .h5, & > .h6,
	& > hr, & > blockquote,
	& > .layout-text {

		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{nth($text-layout-space, 2)});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($text-layout-space--md, 2)}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{nth($text-layout-space--sm, 2)}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal(nth($text-layout-space, 2));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal(nth($text-layout-space--md, 2)); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal((nth($text-layout-space, 2)));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h2, h3, h4, h5, h6, hr {}
	.h2, .h3, .h4, .h5, .h6 {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal((nth($text-layout-space, 2)));

	@include media('<tab-lg') {
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// выравнивание по центру блочных элементов
.push-auto {
	margin-left: auto;
	margin-right: auto; }

// layout страницы
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	//min-height: 100%
	height: 100%;
	max-width: 100%;
	width: $page-layout-width; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	position: relative;
	clear: both;
	z-index: $z-main;
	padding-bottom: $bottom-space--section; }

.main,
.c-heading__align {
	margin-right: $heading-width;
	width: calc(100% - #{$heading-width}); }

//.main__holder
//idth: 100%
//include clear

//.content-wrap
//loat: left
//idth: 100%

.content {
	//margin: 0 200px 0 300px
	min-height: 50px;

	background-color: whitesmoke;

	@include media('<desk-lg') {
		margin-right: 0; }

	@include media('<tab-lg') {
		margin-left: 0; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: fixed;
	left: 0;
	top: 0;
	height: 0;
	width: 100%;
	z-index: $z-header; }

.header__layout {
	position: relative;
	margin: 0 auto; }

.header__holder {
	position: absolute;
	right: 0;
	top: 0;
	height: 100vh;
	width: $heading-width;
	padding: nth($page-layout-space, 1) 60px;
	background-color: $heading-bg;
	z-index: $z-header;
	@include clear; }

//egg
.egg {
	position: absolute;
	top: 100vh;
	right: 0;
	background: url(../../img/egg.png) no-repeat;
	width: 590px;
	height: 450px;
	margin: -450px 0 0;
	z-index: $z-egg;

	display: none; }

//logo
.logo {
	display: block;
	position: relative;
	background: url(../../img/logo.png) no-repeat;
	width: 341px;
	height: 36px;
	text-indent: -9999px;
	overflow: hidden;
	margin-bottom: $bottom-space--section;
	z-index: 5;

	a, & > span {
		display: block;
		height: 100%; } }

/* === HEADER end === */

/* === NAVIGATION === */
.nav {
	position: relative; }

.nav__list {
	position: relative;
	list-style: none;
	margin: 0 0 42px;
	padding: 0;
	@include font($font-size--small, $line-height--small);

	li {
		margin-bottom: 12px;

		&.high {
			@include font-bold;
			@include font(3.6rem, round-float(49/36, 2)); }

		&:last-child {
			margin-bottom: 0; } }

	a {
		color: $base-color;
		text-decoration: none;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	li.current a,
	a:hover {
		text-decoration: none;
		color: $highlight-color; } }

/* === NAVIGATION end === */

/* === FOOTER === */
.footer {
	position: relative;
	z-index: $z-footer;
	background-color: $base-color; }

.footer__holder {
	display: flex;
	flex-wrap: wrap;
	margin-top: -#{$bottom-space--article}; }

.f-box {
	width: calc(100% / 4);
	margin-top: $bottom-space--article;
	padding: 40px; }

.f-box-form {
	width: calc(100% / 2); }

.f-box__title {
	@include font-bold;
	@include font(2.6rem, round-float(30/26, 2));
	margin-bottom: $bottom-space--row; }

/* === FOOTER end === */

